.contentWrapper {
  max-width: 72rem;
  /* height: 53rem; */
  border-radius: 3rem;
  padding: 9.8rem 10.8rem 9.8rem 6.4rem;
  background-color: var(--second-color);
  flex-shrink: 0;
}
.heroWrapper {
  display: flex;
  max-width: 100%;
  gap: 2.4rem;
  margin-bottom: 2.4rem;
}

.heroTitle {
  font-family: var(--font-family);
  font-weight: 500;
  font-size: 4.8rem;
  line-height: 1.17;
  letter-spacing: -0.02em;
  color: var(--main-color);
  margin-bottom: 3.2rem;
}
.part {
  font-style: italic;
  font-weight: 400;
  position: relative;
  z-index: 10;
  padding-left: 0.3rem;
}
.part::before {
  content: '';
  position: absolute;
  top: 0.8rem;
  left: 0;
  width: calc(100% + 0.6rem);
  border-radius: 0.6rem;
  height: 4rem;
  background-color: var(--accent-color);
  z-index: -1;
  display: block;
}
.heroImg {
  display: block;
  border-radius: 3rem;
  max-width: 56.8rem;
  height: 53rem;
  width: 100%;
}
.heroText {
  font-family: var(--font-family);
  font-weight: 400;
  font-size: 1.6rem;
  line-height: 1.37;
  letter-spacing: -0.02em;
  color: var(--main-color);
  margin-bottom: 6.4rem;
}
.heroBtn {
  border-radius: 1.2rem;
  border: none;
  padding: 1.6rem 8.8rem;
  background-color: var(--decorate-color);
  font-family: var(--font-family);
  font-weight: 700;
  font-size: 1.8rem;
  line-height: 1.56;
  color: var(--main-color);
  transition: background-color var(--transition-duration)
    var(--transition-time-function);
}
.imgThumb {
  flex-shrink: 0;
}
.heroBtn:is(:hover, :focus) {
  background-color: var(--accent-color);
}

@media screen and (max-width: 1024px) {
  .heroWrapper {
    max-width: 100%;
    gap: 1.5rem;
    height: auto;
  }
  .contentWrapper {
    padding: 4rem 2rem 4rem 2rem;
    max-width: 55%;
  }
  .imgThumb {
    max-width: 43%;
  }
  .heroImg {
    max-width: 100%;

    object-fit: cover;
  }
  .heroText {
    margin-bottom: 3.2rem;
  }
  .heroTitle {
    margin-bottom: 2rem;
  }
}

@media screen and (max-width: 767px) {
  .heroWrapper {
    display: flex;
    flex-direction: column;
    max-width: 100%;
    gap: 1.6rem;
  }
  .contentWrapper {
    padding: 2rem;
    max-width: 100%;
    text-align: center;
    position: relative;
    z-index: 15;
  }

  .imgThumb {
    max-width: 100%;
  
   
  }
  .heroText {
    margin-bottom: 2rem;
  }
  .heroTitle {
    margin-bottom: 2rem;
  }
  .heroImg {
    object-fit: cover;
  }
  .part {
    position: static;
  }
  .part::before {
    top: 14rem;
    left: 17rem;
    width: 47%;
   
   
  }
  .heroWrapper {
    margin-bottom: 1.6rem;
  }
}
